export default {
  data: function data() {
    return {
      host: window.location.origin,
      publishDetail: {}
    };
  },
  mounted: function mounted() {
    this.getDetail();
  },
  methods: {
    goBack: function goBack() {
      this.$router.go(-1);
    },
    getDetail: function getDetail() {
      var _this = this;

      this.$apiCall('api.OfferProduct.getDetail', {
        id: this.$route.query.id
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this.publishDetail = r.Data.Results;
        }
      });
    }
  }
};